.wpte-product-table-layout-table{
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border-radius: 10px;
    overflow: hidden;
    text-align: center;
    box-shadow: 0 2px 12px rgba(32, 32, 32, .3);
    th, td{
        padding: 12px 15px;
        font-size: 16px;
        border: none;
    }
    th{
        background: rgb(245, 114, 114);
    }
    tr:nth-child(even){
        background: #eeeeee;
    }
    td{
        .wpte-product-table-layout-image{
            height: 80px;
            width: 80px;
            display: flex;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;
            border-radius: 2px 2px 2px 2px;
            background: rgba(255,255,255,1.00);
            border-style: solid;
            border-width: 1px 1px 1px 1px;
            border-color: #038a96;
            padding: 8px 8px 8px 8px;
            img{
                width: 100%;
                height: 100%;
                border-radius: 2px 2px 2px 2px;
            }
        }

        /* Title */
        .wpte-product-table-layout-title{
            display: block;
            font-size: 16px;
            margin: 0;
            padding: 0;
            a{
                font-family: Arial;
                color: #038a96;;
                font-size: 16px;
                font-weight: normal;
                line-height: 1.33;
                padding: 5px 0;
            }
            a:hover{
                color: #13bdcd;
            }
        }

        /* Description */
        .wpte-product-table-layout-description{
            color: #4d4b4b;
            font-size: 16px;
            font-family: Arial;
            font-weight: normal;
            padding: 12px 15px 12px 15px;
        }

        /* Price */
        .wpte-product-table-layouts-price{
            font-size: 16px;
            font-weight: none;
            line-height: 1.5em;
        }

        /* Quantity */
        .wpte-product-table-layout-quantity{
            input{
                max-width: 70px;
                width: 100%;
                height: 100%;
                min-height: 0;
                line-height: 2;
                background: #fafafa;
                color: #000000;
                border-style: solid;
                border-width: 2px 2px 2px 2px;
                border-color: #188791;
                border-radius: 5px 5px 5px 5px;
                padding: 3px 5px 3px 5px;
            }
            input:active, input:focus{
                outline: 0;
                border: 0;
                box-shadow: 0;
            }
        }

        /* Cart */
        .wpte-table-layout-product-cart{
            a, .button{
                min-height: 0 !important;
                line-height: 1 !important;
                background: rgba(24,135,145,1.00);
                color: #ffffff;
                font-size: 15px;
                text-transform: none;
                border-style: solid;
                border-width: 0px 0px 0px 0px;
                border-radius: 10px 10px 10px 10px;
                padding: 10px 15px 10px 15px;
                .wpte-icon{
                    margin-right: 5px;
                }
            }
        }
    }
}

.wpte-product-table-for-sm{
    display: none;
}
.wpte-product-table-for-lg{
    display: revert;
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 575.98px) { 
    .wpte-product-table-for-sm{
        display: revert;
    }
    .wpte-product-table-for-lg{
        display: none;
    }

    .wpte-product-table-for-sm{
        .wpte-table-layout-icons-cart,
        .wpte-product-table-layout-price{
            margin: 10px 0;
        }
    }
}
  
/* Medium devices (landscape Tab, 576px and up) */
@media (min-width: 576px) and (max-width: 768px) { 
    .wpte-product-table-for-sm{
        display: revert;
    }
    .wpte-product-table-for-lg{
        display: none;
    }
    .wpte-product-table-for-sm{
        .wpte-product-table-layout-price{
            margin: 10px 0;
        }
        .wpte-product-table-layout-group{
            display: flex;
            align-items: center;
        }
        .wpte-table-layout-icons-cart{
            margin: 10px 15px;
        }
    }
}
