.wpte-product-container {
  .wpte-product-hover-style-caption {
    width: 100%;
    margin: 0 auto;
    position: relative;
    overflow: hidden;

    .wpte-product-hover {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }

    /* Wish List*/
    .wpte-product-layouts-wish-list {
      position: absolute;
      top: 15px;
      right: 15px;
      .wpte-product-tooltip * {
        color: rgb(30, 30, 30);
        font-size: 16px;
        transition: none;
      }
      .wpte-product-tooltip::after {
        transition: none;
      }
      .wpte-product-wishlist-icon * {
        color: #fff;
        font-size: 18px;
        transition: none;
        line-height: 1;
      }
    }

    /* Category */
    .wpte-product-category {
      text-align: center;
      a {
        font-size: 14px;
        color: #ffffff;
        display: inline-block;
        margin: 0 5px;
        transition: none;
      }
      a:hover {
        color: #bebcbc;
      }
    }

    /* Title */
    .wpte-product-title {
      width: 100%;
      font-size: 22px;
      text-align: center;
      margin: 10px 0;
      a {
        font-size: 22px;
        font-weight: 600;
        color: #ffffff;
        line-height: 1;
        transition: none;
      }
      a:hover {
        color: #bebcbc;
      }
    }

    /* Description */
    .wpte-product-content {
      width: 100%;
      font-size: 18px;
      padding-bottom: 5px;
      color: #ffffff;
      text-align: center;
      line-height: 1;
    }

    /* Rating */
    .wpte-product-rating {
      padding: 5px 0;
      line-height: 1;
      .wpte-icons {
        font-size: 18px;
        color: #ffa500;
        line-height: 1;
        padding: 0px 2px 0px 2px;
      }
    }

    /* Price */
    .wpte-product-price {
      font-size: 16px;
      font-weight: 600;
      line-height: 1.5em;
      padding: 0px 0px 15px 0px;
    }

    /* Button */
    .wpte-product-button {
      .button,
      a {
        display: inline-block;
        font-size: 16px;
        text-transform: none;
        line-height: 1;
        border: 0;
        border-radius: 50px 50px 50px 50px;
        padding: 10px 15px 10px 15px;
        transition: none;
        min-height: auto;
        i,
        .wpte-icon {
          margin-right: 5px;
          transition: none;
        }
        span {
          transition: none;
        }
      }
    }
  }
  .wpte-product-hover-style-caption::after {
    padding-bottom: 100%;
    content: '';
    display: block;
  }
  .wpte-product-caption-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: transparent;
    color: #fff;
    backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    transform: translateZ(0);

    .wpte-product-hover-figure {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }
    .wpte-product-hover-image {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 1;
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
    .wpte-product-hover-figure-caption {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      color: #ffffff;
    }
    .wpte-product-hover-caption-tab {
      background: transparent;
    }
  }
  .wpte-product-caption-hover:hover,
  .wpte-product-caption-hover .wpte-touch {
    z-index: 5;
  }
  .wpte-product-caption-hover:before,
  .wpte-product-caption-hover:after,
  .wpte-product-caption-hover *,
  .wpte-product-caption-hover *:before,
  .wpte-product-caption-hover *:after {
    box-sizing: border-box;
    transition: all 0.35s ease;
  }
  .wpte-product-hover-caption-tab {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 15px;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  [class^='wpte-product-caption-']:before,
  [class*='wpte-product-caption-']:before,
  [class*='wpte-product-caption-']:after,
  [class*='wpte-product-caption-']:after {
    z-index: 2;
  }
  [class^='wpte-product-caption-'] .wpte-product-hover-figure-caption,
  [class*='wpte-product-caption-'] .wpte-product-hover-figure-caption {
    z-index: 3;
  }
}
