.wpte-product-general-column{
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}
.wpte-general-layout-row {
    text-align: left;
    border-radius: 0px 0px 0px 0px;
    padding: 6px 6px 6px 6px;
	box-sizing: border-box;
    img{
        width: 100%;
        height: auto;
    }
	&:hover .wpte-general-layout-product-hover-image,
	.wpte-general-layout-product-gallery-thumb{
		line-height: 0;
		display: none;
	}
	&:hover {
		.wpte-general-layout-product-hover-g-image{
			display: block;
		}
	}
	.wpte-general-layout-image-area{
		position: relative;
		overflow: hidden;
	}
	.wpte-general-layout-product-thumb{
		line-height: 0;
	}

	/* Icons */
	.wpte-general-layout-product-icons{
		position: absolute;
		display: flex;
		box-sizing: border-box;
		grid-gap: 8px;
		padding: 15px 15px 15px 15px;
	}
	.wpte-general-layout-icons {
		a,
		.button{
			display: flex;
			align-items: center;
			justify-content: center;
			box-sizing: border-box;
			font-size: 16px;
			min-width: 40px;
			min-height: 40px;
			color: #36b6d9;
			background: #fff;
			border-radius: 0;
			cursor: pointer;
		}
		a:hover,
		.button:hover{
			color: #fff;
			background: #36b6d9;
		}
	}

	/* Right */
	.wpte-general-layout-icon-right {
		height: 100%;
		width: auto;
		right: 0;
		top: 0;
		flex-direction: column;
		justify-content: end;

		.wpte-general-layout-product-cart{
			transform: translateX(calc( 100% + 100px ));
			transition: all 0.3s;
			visibility: hidden;
		}
		.wpte-general-layout-product-view{
			transform: translateX(calc( 100% + 100px ));
			transition: all 0.5s;
			visibility: hidden;
		}
		.wpte-general-layout-product-compare{
			transform: translateX(calc( 100% + 100px ));
			transition: all 0.7s;
			visibility: hidden;
		}
		.wpte-general-layout-product-wishlist{
			transform: translateX(calc( 100% + 100px ));
			transition: all 0.8s;
			visibility: hidden;
		}
	}

	&:hover {
		.wpte-general-layout-icon-right {
			.wpte-general-layout-product-cart{
				transform: translateX(0);
				visibility: visible;
			}
			.wpte-general-layout-product-view{
				transform: translateX(0);
				visibility: visible;
			}
			.wpte-general-layout-product-compare{
				transform: translateX(0);
				visibility: visible;
			}
			.wpte-general-layout-product-wishlist{
				transform: translateX(0);
				visibility: visible;
			}
		} 
	}

	 /* Bottom */
	 .wpte-general-layout-icon-bottom{
        width: 100%;
        bottom: 0;
        .wpte-general-layout-product-cart{
            transform: translateY(calc( 100% + 100px ));
            transition: all 0.3s;
            visibility: hidden;
        }
        .wpte-general-layout-product-view{
            transform: translateY(calc( 100% + 100px ));
            transition: all 0.5s;
            visibility: hidden;
        }
        .wpte-general-layout-product-compare{
            transform: translateY(calc( 100% + 100px ));
            transition: all 0.7s;
            visibility: hidden;
        }
        .wpte-general-layout-product-wishlist{
            transform: translateY(calc( 100% + 100px ));
            transition: all 0.8s;
            visibility: hidden;
        }
    } 

    &:hover{
        .wpte-general-layout-icon-bottom{
            .wpte-general-layout-product-cart{
                transform: translateY(0);
                visibility: visible;
            }
            .wpte-general-layout-product-view{
                transform: translateY(0);
                visibility: visible;
            }
            .wpte-general-layout-product-compare{
                transform: translateY(0);
                visibility: visible;
            }
            .wpte-general-layout-product-wishlist{
                transform: translateY(0);
                visibility: visible;
            }
        }
        .wpte-general-layout-product-hover-g-image{
            display: block;
        }
    }
	/* End Icons */

	/* Category */
	.wpte-general-layout-category-area {
		a{
			display: inline-block;
			color: #767676;
			font-size: 14px;
			font-family: Arial;
			padding: 5px 0;
			&:hover{
				font-size: 14px;
				color: #4f4a4a;
				display: inline-block;
			}
		}
	}
	/* Title */
	.wpte-general-layout-title-area {
		.wpte-general-layout-product-title{
			display: block;
			font-size: 18px;
			margin: 0;
			padding: 0;
			a{
				font-family: Arial;
				color: #444343;
				font-size: 18px;
				font-weight: normal;
				line-height: 1.33;
				padding: 5px 0;
				&:hover{
					color: #444343;
				}
			}
		}
	}
	/* Rating */
	.wpte-general-layout-rating-area{
		padding: 5px 0;
		line-height: 1;
		.wpte-icons{
			font-size: 16px;
			color: #ffa500;
			line-height: 1;
			padding: 5px 2px 6px 2px;
		}
	}

	/* Price */
	.wpte-general-layout-price-area {
		color: #808080;
		font-size: 14px;
		font-family: Arial;
		font-weight: 600;
		line-height: 1.5em;
	}

	/* Badge */
	.wpte-general-layout-image-area{
		ul{
			margin: 0;
			width: 100%;
			display: flex;
			align-items: start;
			flex-direction: column;
			position: absolute;
			top: 0;
			box-sizing: border-box;
			padding: 15px 0px 0px 10px;
			.product-label{
				min-width: 54px;
				min-height: 20px;
				color: #FFF;
				background-color: #ff6f61;
				font-size: 14px;
				font-weight: 600;
				text-transform: normal;
				text-align: center;
				word-break: break-all;
				letter-spacing: .4px;
				line-height: 1.2;
				list-style: none;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 20px 20px 20px 20px;
				padding: 5px 5px 5px 5px;
				margin-bottom: 10px;
				z-index: 2;
			}
			.featured{
				background: rgba(247,69,69,0.84);
				color: #ffffff;
			}
			.onsale {
				background: rgba(54,182,217,1.00);
				color: #ffffff;
			}
		}
	}
}
