.wpte-product-call-to-column{
    grid-column-gap: 30px;
    grid-row-gap: 30px;
}
.wpte-call-to-action-layout-row{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: rgba(255,255,255,1.00);
    box-shadow: 0px 0px 15px 2px rgb(209 209 209);
    border-radius: 5px 5px 5px 5px;
    img{
        width: 100%;
        height: auto;
    }
    .wpte-call-to-action-content-wrapper{
        padding: 20px 20px 20px 20px;
        .wpte-call-to-action-content{
            text-align: center;
            border-radius: 5px 5px 5px 5px;
            padding: 45px 0px 0px 0px;
        }
    }
} 

.wpte-call-to-action-layout-row:hover .wpte-call-to-action-layout-product-hover-image,
.wpte-call-to-action-layout-product-gallery-thumb{
    line-height: 0;
    display: none;
}
.wpte-call-to-action-layout-row:hover{
    .wpte-call-to-action-layout-product-hover-g-image{
        display: block;
    }
}

.wpte-call-to-action-layout-image-area{
    position: relative;
    overflow: hidden;
}

.wpte-call-to-action-layout-product-thumb,
.wpte-call-to-action-layout-product-gallery-thumb{
    background-repeat: no-repeat;
    max-width: 100%;
    width: 100%;
    min-height: 300px;
    height: 100%;
    background-size: cover;
    background-position: center;
    line-height: 0;
}

.wpte-call-to-action-content{
    height: 100%;
    box-sizing: border-box;
}

/* Icons */

.wpte-call-to-action-layout-product-icons{
    position: absolute;
    display: flex;
    box-sizing: border-box;
    grid-gap: 10px;
    justify-content: center;
    padding: 0px 0px 15px 0px;
}

.wpte-call-to-action-layout-icons{
    a, .button{
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        font-size: 16px;
        min-width: 40px;
        min-height: 40px;
        color: #444;
        background: #fff;
        border-radius: 50%;
        cursor: pointer;
    }
    a:hover, .button:hover{
        color: #fff;
        background: #ff6f61;
    }
}

/* Bottom */
.wpte-call-to-action-layout-icon-bottom{
    width: 100%;
    bottom: 0;
    .wpte-call-to-action-layout-product-cart{
        transform: translateY(calc( 100% + 100px ));
        transition: all 0.3s;
        visibility: hidden;
    }
    .wpte-call-to-action-layout-product-view{
        transform: translateY(calc( 100% + 100px ));
        transition: all 0.5s;
        visibility: hidden;
    }
    .wpte-call-to-action-layout-product-compare{
        transform: translateY(calc( 100% + 100px ));
        transition: all 0.7s;
        visibility: hidden;
    }
    .wpte-call-to-action-layout-product-wishlist{
        transform: translateY(calc( 100% + 100px ));
        transition: all 0.8s;
        visibility: hidden;
    }
}

.wpte-call-to-action-layout-row:hover{
    .wpte-call-to-action-layout-icon-bottom{
        .wpte-call-to-action-layout-product-cart{
            transform: translateY(0);
            visibility: visible;
        }
        .wpte-call-to-action-layout-product-view{
            transform: translateY(0);
            visibility: visible;
        }
        .wpte-call-to-action-layout-product-compare{
            transform: translateY(0);
            visibility: visible;
        }
        .wpte-call-to-action-layout-product-wishlist{
            transform: translateY(0);
            visibility: visible;
        }
    }
}
/* End Icons */

/* Category */
.wpte-call-to-action-layout-category-area{
	a{
		display: inline-block;
		color: #767676;
		font-size: 14px;
		font-family: Arial;
		padding: 5px 0;
		&:hover{
			font-size: 14px;
			color: #4f4a4a;
			display: inline-block;
		}
	}
}

/* Title */
.wpte-call-to-action-layout-title-area{
    .wpte-call-to-action-layout-product-title{
        display: block;
		font-size: 18px;
		margin: 0;
		padding: 0;
		a{
			font-family: Arial;
			color: #444343;
			font-size: 18px;
			font-weight: normal;
			line-height: 1.33;
			padding: 5px 0;
			&:hover{
				color: #444343;
			}
		}
    }
}

/* Rating */
.wpte-call-to-action-layout-rating-area{
	padding: 5px 0;
	line-height: 1;
	.wpte-icons{
		font-size: 16px;
		color: #ffa500;
		line-height: 1;
		padding: 5px 2px 6px 2px;
	}
}

/* Price */
.wpte-call-to-action-layout-price-area{
	color: #808080;
	font-size: 14px;
	font-family: Arial;
	font-weight: 600;
	line-height: 1.5em;
	padding: 0px 0px 20px 0px;
}

/* Badge */
.wpte-call-to-action-layout-image-area{
    ul{
        margin: 0;
        width: 100%;
        display: flex;
        align-items: start;
        flex-direction: column;
        position: absolute;
        top: 0;
        box-sizing: border-box;
        padding: 15px 0px 0px 10px;
        .product-label{
            min-width: 54px;
            min-height: 20px;
            color: #FFF;
            background-color: #ff6f61;
            font-size: 14px;
            font-weight: 600;
            text-transform: normal;
            text-align: center;
            word-break: break-all;
            letter-spacing: .4px;
            line-height: 1;
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 20px 20px 20px 20px;
            box-sizing: border-box;
            padding: 5px 5px 5px 5px;
            margin-bottom: 10px;
        }
        .featured{
            background: rgba(247,69,69,0.84);
            color: #ffffff;
        }
        .onsale {
            background: rgba(54,182,217,1.00);
            color: #ffffff;
        }
    }
}
/* Button */
.wpte-call-to-action-layout-product-cart{
    
    .button, 
    a{
        display: inline-block;
        font-size: 16px;
        text-transform: none;
        border-style: solid;
        border-width: 1px 1px 1px 1px;
        border-color: #83b735;
        border-radius: 50px 50px 50px 50px;
        padding: 10px 20px 10px 20px;
        .wpte-icon{
            margin-right: 10px;
        }
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 575.98px) { 
    .wpte-call-to-action-layout-row{
        grid-template-columns: 1fr;
    }
}

/* Medium devices (landscape Tab, 576px and up) */
@media (min-width: 576px) and (max-width: 768px) { 
    .wpte-call-to-action-layout-row{
        grid-template-columns: 1fr;
    }
}