
.wpte-product-layouts-tabs-style-1-list{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	margin: 0;
	li{
		list-style: none;
		color: #fff;
		background: #83b735;
		font-size: 16px;
		line-height: 1;
		border-radius: 30px;
		padding: 10px 20px;
		margin: 40px 20px 40px 0;
		cursor: pointer;
	}
	li:hover{
		background: #669125;
	}
	.wpte-tab-active{
		background: #da3f3f;
	}
}

