/* Tooltip */
.wpte-product-tooltip {
  background: #fff;
  text-align: right;
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  text-transform: none;
  white-space: nowrap;
  display: inline-block;
  position: absolute;
  padding: 5px 10px;
  left: auto;
  right: 100%;
  top: 50%;
  transform: translate(-30px, -50%);
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: 350ms ease 0s;
  border: 1px solid rgb(255, 134, 64);
  border-radius: 5px;
}

.wpte-product-tooltip::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: auto;
  right: -6px;
  top: 50%;
  bottom: auto;
  margin-top: -5px;
  border-right-width: 0;
  border-bottom: 5px solid transparent;
  border-left: 6px solid rgb(255, 134, 64);
  border-top: 5px solid transparent;
}

.wpte-product-tooltip-area:hover .wpte-product-tooltip {
  transform: translate(-10px, -50%);
  opacity: 1;
  visibility: visible;
}

/* Top */
.wpte-tooltip-top {
  text-align: center;
  right: 50%;
  top: 100%;
  transform: translate(50%, 100%);
}

.wpte-tooltip-top::after {
  left: 50%;
  right: 0;
  top: -10px;
  bottom: auto;
  transform: translateX(-50%);
  margin-top: 0;
  border-right: 5px solid transparent;
  border-bottom: 5px solid rgb(255, 134, 64);
  border-left: 5px solid transparent;
  border-top: 5px solid transparent;
}

.wpte-product-tooltip-area:hover .wpte-tooltip-top {
  transform: translate(50%, 40%);
  opacity: 1;
  visibility: visible;
}

/* Left */
.wpte-tooltip-left {
  text-align: left;
  right: -50px;
  top: 50%;
  transform: translate(100%, -50%);
}

.wpte-tooltip-left::after {
  left: -11px;
  top: 50%;
  border-right: 7px solid rgb(255, 134, 64);
  border-left: 5px solid transparent;
}

.wpte-product-tooltip-area:hover .wpte-tooltip-left {
  transform: translate(60%, -50%);
  opacity: 1;
  visibility: visible;
}

/* Bottom */
.wpte-tooltip-bottom {
  text-align: center;
  right: 50%;
  top: -50px;
  bottom: auto;
  transform: translate(50%, -100%);
}

.wpte-tooltip-bottom::after {
  left: 50%;
  right: 0;
  top: auto;
  bottom: -11px;
  transform: translateX(-50%);
  margin-top: 0;
  border-right: 5px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 5px solid transparent;
  border-top: 5px solid rgb(255, 134, 64);
}

.wpte-product-tooltip-area:hover .wpte-tooltip-bottom {
  transform: translate(50%, 20%);
  opacity: 1;
  visibility: visible;
}

