}
.wpte-product-filter-wrapper .wpte-product-filter-items .wpte-product-filter-item .wpte-filter-option .radio-label::before {
  content: "";
  top: 2.5px;
  left: 0;
  width: 15px;
  height: 15px;
  position: absolute;
  border: 1px solid #aa4098;
  border-radius: 50%;
  background-color: #ffffff;
}
.wpte-product-filter-wrapper .wpte-product-filter-items .wpte-product-filter-item .wpte-filter-option input[type=radio]:not(:checked) + .radio-label:after {
  opacity: 0;
  transform: scale(0);
}
