.wpte-product-container{
    .easing_easeInOutExpo{
        * {
            -webkit-transition-timing-function: cubic-bezier(1, 0, 0, 1);
            transition-timing-function: cubic-bezier(1, 0, 0, 1);
        }
    }
    .easing_easeInOutCirc{
        * {
            -webkit-transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
            transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
        }
    }
    .easing_easeOutBack{
        * {
            -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
            transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
        }
    }
}

.wpte-product-flipbox-column{
    transform-style: preserve-3d;
    *{
        transition: all 500ms;
    }
}


.wpte-product-layout-flipbox{
    width: 100%;
    margin: 0 auto;
    position: relative;
    -webkit-perspective: 1000px;
    -moz-perspective: 1000px;
    perspective: 1000px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-transition-property: none;
    transition-property: none;
    -webkit-transition-duration: 500ms;
    transition-duration: 500ms;
    .wpte-product-layout-flipbox-hover,
    .wpte-product-layout-flipbox-figure{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    .wpte-product-layout-flipbox-front-section,
    .wpte-product-layout-flipbox-back-section{
        background:  #0056b3;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        overflow: hidden;
        img{
            width: 100%;
            height: auto;
        }
    }

    .wpte-product-layout-flipbox-frontend{
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        pointer-events: none;
        backface-visibility: hidden;
    }
    .wpte-product-layout-flipbox-frontend:before{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        content: "";
        display: block;
        pointer-events: none;
        z-index: 11;
    }

    .wpte-product-layout-flipbox-backend{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        backface-visibility: hidden;
    }
    .wpte-product-layout-flipbox-backend:before{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        content: "";
        pointer-events: none;
        z-index: 11;
        display: block;
    }

    .wpte-product-layout-flipbox-frontend:after,
    .wpte-product-layout-flipbox-backend:after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        display: block;
        background: transparent;
        z-index: -1;
    }

    .wpte-product-layout-flipbox-hover{

        .wpte-product-flip-bottom-to-top{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateX(-180deg);
                transform: rotateX(-180deg);
            }
        }

        .wpte-product-flip-left-to-right{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateY(-180deg);
                transform: rotateY(-180deg);
            }
        }

        .wpte-product-flip-right-to-left{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
            }
        }

        .wpte-product-flip-top-to-bottom{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
            }
        }
        .wpte-product-flip-top-to-bottom{
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
            }
        }
    }

    .wpte-product-layout-flipbox-hover:hover{
        .wpte-product-layout-flipbox-figure{
            .wpte-product-layout-flipbox-frontend{
                z-index: -1;
            }
            .wpte-product-layout-flipbox-backend{
                z-index: 1;
            }
        }
        .wpte-product-flip-bottom-to-top{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
            }
        }
        .wpte-product-flip-left-to-right{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
            }
        }

        .wpte-product-flip-right-to-left{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateY(-180deg);
                transform: rotateY(-180deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
            }
        }

        .wpte-product-flip-top-to-bottom{
            .wpte-product-layout-flipbox-frontend{
                -webkit-transform: rotateX(-180deg);
                transform: rotateX(-180deg);
            }
            .wpte-product-layout-flipbox-backend{
                -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
            }
        }
    }

    /* Category */
    .wpte-product-category {
        text-align: center;
        a{
            font-size: 14px;
            color: #ffffff;
            display: inline-block;
            margin: 0 5px;
            transition: none;
        }
        a:hover{
            color: #bebcbc;
        }
    }

    /* Title */
    .wpte-product-title {
        width: 100%;
        font-size: 22px;
        text-align: center;
        margin: 10px 0;
        a{
            font-size: 22px;
            font-weight: 600;
            color: #ffffff;
            line-height: 1;
            transition: none;
        }
        a:hover{
            color: #bebcbc;
        }
    }

    /* Description */
    .wpte-product-content {
        width: 100%;
        font-size: 18px;
        padding-bottom: 5px;
        color: #ffffff;
        text-align: center;
        line-height: 1;
    }

    /* Rating */
    .wpte-product-rating{
        padding: 5px 0;
        line-height: 1;
        .wpte-icons{
            font-size: 18px;
            color: #ffa500;
            line-height: 1;
            padding: 0px 2px 0px 2px;
        }
    }

    /* Price */
    .wpte-product-price{
        font-size: 16px;
        font-weight: 600;
        line-height: 1.5em;
        padding: 0px 0px 15px 0px;
    }

    /* Button */
    .wpte-product-button {
        .button, a{
            display: inline-block;
            font-size: 16px;
            text-transform: none;
            line-height: 1;
            border: 0;
            border-radius: 50px 50px 50px 50px;
            padding: 10px 15px 10px 15px;
            transition: none;
            min-height: auto;
            i, .wpte-icon{
                margin-right: 5px;
                transition: none;
            }
            span{
                transition: none;
            }
        }
    }
}

.wpte-product-layout-flipbox:after {
    padding-bottom: 100%;
    content: "";
    display: block;
}