.wpte-product-compare-wrapper{
    width: 100%;
    position: relative;
}
.wpte-product-compare-wrapper a{
    text-decoration: none;
}
.wpte-product-compare-wrapper a:focus{
    border: none;
    box-shadow: none;
    outline: none;
}

#wpte-product-compare-box{
    width: 85%;
    max-height: 600px;
    padding: 0;
}

/* Compare Table */
#wpte-product-compare-box article {
    width:100%;
    margin:0 auto;
    position:relative;
    overflow-x:auto;
}

.wpte-compare-table-title{
    position: relative;
    left: 0;
    right: 0;
    width: 100%;
    
}
#wpte-product-compare-box h1{
    display: block;
    background: rgba(16,49,120,1);
    text-align: left;
    font-size: 18px;
    color: #fff;
    padding: 18px;
    text-transform: uppercase;
    margin: 0;
    line-height: 1;
}

#wpte-product-compare-box table { 
      width:100%;
      margin: 0;
}

#wpte-product-compare-box tbody th{
    max-width: 150px;
    min-width: 150px;
    overflow: auto;
    text-align: left;
    color: #333;
    font-weight: 700;
    font-size: 14px;
}
#wpte-product-compare-box td{
    color: rgba(91,108,143,1);
    font-size: 14px;
    height:70px;
    width: 250px;
    max-width: 250px;
    min-width: 250px;
}


#wpte-product-compare-box tbody th,
#wpte-product-compare-box tbody td{
    padding: 20px;
    border-color: rgba(240,242,245,1);
    border-width: 0 0 1px 1px;
    border-style: solid;
    padding: 20px;
}
#wpte-product-compare-box tbody th{
    color: rgba(16,49,120,1);
    border-top-width: 1px;
    border-bottom-width: 0;
}

#wpte-product-compare-box tbody td:last-child{
    border-right-width: 1px;
}



#wpte-product-compare-box td,
#wpte-product-compare-box th {
    text-align:left;
}

#wpte-product-compare-box .hide {
border:0;
background:none;
}

#wpte-product-compare-box .wpte-product-popup-close{
    color: #fff;
}

/* Remove Button */
#wpte-product-compare-box .wpte-compare-product-remove{
    text-align: center;
    padding-bottom: 10px;
    
}
#wpte-product-compare-box .wpte-compare-product-remove a{
    font-size: 20px;
    cursor: pointer;
    color: rgba(16,49,120,1);
}

/* Image */
#wpte-product-compare-box .wpte-compare-product-img img{
    width: 100%;
    height: auto;
}

/* Categories */
#wpte-product-compare-box .wpte-compare-product-cat{
    padding: 10px 0;
}
#wpte-product-compare-box .wpte-compare-product-cat a{
    color: rgba(91,108,143,1);
    font-size: 14px;
    font-weight: normal;
}

/* Price */
#wpte-product-compare-box .wpte-compare-product-price{
    font-size: 18px;
    color: rgba(16,49,120,1);
    font-weight: 500;
    line-height: 1;
    margin-bottom: 14px;
    margin-top: 4px;
}

/* Cart Button */
#wpte-product-compare-box .wpte-compare-product-button{
    margin: 20px 0px 30px 0;
}
#wpte-product-compare-box .wpte-compare-product-button .button{
    text-transform: none;
    font-size: 16px;
    padding: 10px 30px;
    border-radius: 20px 20px;
}

@media (max-width: 767px) {
    #wpte-product-compare-box  td, 
    #wpte-product-compare-box th {
        width: 250px;
    }
    
}
  
  @media (max-width: 639px) {
    
    #wpte-product-compare-box  td, 
    #wpte-product-compare-box th {
        width: 250px;
    }

}
