.wpte-product-layouts-carousel-wrapper{
	position: relative;
	// Dots
	.wpte-swiper-pagination{
		position: absolute;
		text-align: center;
		z-index: 1;
		.swiper-pagination-bullet{
			width: 10px;
			height: 10px;
			opacity: 1 !important;
		}
	}
	// Icon
	.wpte_carousel_arrows{
		display: inline-block;
		position: absolute;
		z-index: 999;
		cursor: pointer;
	}
	.wpte_carousel_prev{
		left: 0;
	}
	.wpte_carousel_next{
		right: 0;
	}
}