.wpte-product-list-layout-column{
	grid-column-gap: 20px;
	grid-row-gap: 20px;
}
.wpte-product-list-product-image{
	max-width: 150px;
	width: 100%;
	height: 100%;
	margin-right: 20px;
	box-sizing: border-box;
	.wpte-product-list-img{
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #dbdbf7;
		padding: 5px;
		img{
			width: 100%;
			height: 100%;
		}
	}
}
.wpte-product-list-product-text{
	width: 100%;
	.wpte-product-list-product-title{
		display: block;
		font-size: 18px;
		margin: 0;
		padding: 0;
		a{
			font-family: Lato;
			color: #575757;
			font-size: 18px;
			font-weight: normal;
			line-height: 1.33;
			padding: 5px 0;
		}
		a:hover{
			color: #444343;
		}
	}
	.wpte-product-list-product-description{
		display: block;
		font-size: 14px;
		p{
			font-family: Lato;
			color: #747474;
			font-size: 14px;
			font-weight: normal;
			line-height: 1.33;
			padding: 5px 0;
		}
	}
}
.wpte-product-list-product-price{
	width: 50%;
	font-size: 18px;
	font-weight: 500;
	color: #575757;
}
.wpte-product-list-cart-area{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: end;
	align-items: end;
	.wpte-product-list-product-quantity{
		margin-bottom: 5px;
		.quantity{
			input{
				max-width: 70px;
				width: 100%;
				background: #dbdbf7;
				color: #575757;
				text-align: center;
				line-height: 1;
				border: 0;
				border-radius: 20px;
				padding: 4px 5px 4px 10px;
			}
		}
	}
	.wpte-product-list-product-button{
		.wpte-product-cart-icon-render{
			display: block;
			span{
				display: inline-flex;
			}
			a,
			.button{
				background: #bcd8f7;
				color: #575757;
				font-size: 15px;
				font-weight: normal;
				text-transform: none;
				line-height: 1;
				border-width: 0;
				border-radius: 50px;
				padding: 10px 15px 10px 15px;
				.wpte-icon{
					margin-right: 5px;
				}
			}
		}
	}
}