.wpte-product-container {
  width: 100%;
  .wpte-icons {
    display: inline-block;
  }
  del {
    opacity: 0.5;
  }
  .wpte-disabled {
    opacity: 0.5;
    cursor: pointer;
  }
  .wpte-disabled {
    a {
      pointer-events: none;
    }
  }
  ins,
  mark {
    background: 0 0;
    text-decoration: none;
  }
  .wpte-product-hidden {
    opacity: 0;
  }
  .wpte-product-visible,
  .wpte-product-hidden.wpte-product-visible {
    opacity: 1;
  }
}

.wpte-product-container * {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    display: inline-block;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
    border-bottom: 0px;
    border-left: 0px;
    border-right: 0px;
    border-top: 0px;
  }
}

.wpte-product-container {
  .wpte-product-row {
    a,
    a:hover {
      text-decoration: none;
      box-sizing: border-box;
    }
    button {
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
      box-sizing: border-box;
    }
    span,
    li {
      line-height: 1;
      box-sizing: border-box;
    }
    .button {
      line-height: 1;
      min-height: 0;
    }
  }
}

// Button
.wpte-btn {
  cursor: pointer;
  width: max-content;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 8px 12px;
  border-radius: 5px;
  border: 1px solid transparent;
  line-height: 1;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  font-size: 0.875rem;
  &:hover {
    color: #fff;
  }
}

.wpte-btn.wpte-btn-success {
  background-color: #00d084;
  border-color: #00d084;
  &:hover {
    background-color: #02b171;
  }
}
.wpte-btn.wpte-btn-warning {
  background-color: #f9d6b7;
  color: #a65300;
}
.wpte-btn.wpte-btn-primary {
  background-color: #4280d0;
  border-color: #4280d0;
}

/* Column */
.wpte-col-lap-1 {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.wpte-col-lap-2 {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.wpte-col-lap-3 {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.wpte-col-lap-4 {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
}

.wpte-col-lap-5 {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
}

.wpte-col-lap-6 {
