}

/* Effects */
.wpte-product-container {
  .wpte-product-hover {
    .wpte-fade-up,
    a.wpte-fade-up {
      transform: translate(0, 25px);
    }
    .wpte-fade-down,
    a.wpte-fade-down {
      transform: translate(0, -25px);
    }
    .wpte-fade-left {
      transform: translate(25px, 0);
    }
    .wpte-fade-right {
      transform: translate(-25px, 0);
    }
    .wpte-fade-up-big {
      transform: translate(0, 300%);
    }
    .wpte-fade-down-big {
      transform: translate(0, -300%);
    }
    .wpte-fade-left-big {
      transform: translate(150%, 0);
    }
    .wpte-fade-right-big {
      transform: translate(-150%, 0);
    }
    .wpte-zoom-in {
      transform: scale(0.5);
    }
    .wpte-zoom-out {
      transform: scale(1.5);
    }
    .wpte-flip-x {
      transform: rotateX(-90deg);
    }
    .wpte-flip-y {
      transform: rotateY(90deg);
    }

    .wpte-fade-up,
    .wpte-fade-down,
    .wpte-fade-left,
    .wpte-fade-right,
    .wpte-fade-up-big,
    .wpte-fade-down-big,
    .wpte-fade-left-big,
    .wpte-fade-right-big,
    .wpte-zoom-in,
    .wpte-zoom-out,
    .wpte-flip-x,
    .wpte-flip-y {
      opacity: 0;
    }
  }

  .wpte-product-hover:hover {
    .wpte-fade-up,
    .wpte-fade-down,
    .wpte-fade-left,
    .wpte-fade-right,
    .wpte-fade-up-big,
    .wpte-fade-down-big,
    .wpte-fade-left-big,
    .wpte-fade-right-big,
    .wpte-zoom-in,
    .wpte-zoom-out,
    .wpte-flip-x,
    .wpte-flip-y {
      transform: translate(0, 0) scale(1) rotate(0);
      opacity: 1;
    }
    .wpte-product-hover-delay-xs {
      transition-delay: 0.175s !important;
    }
    .wpte-product-hover-delay-sm {
      transition-delay: 0.35s !important;
    }
    .wpte-product-hover-delay-md {
      transition-delay: 0.525s !important;
    }
    .wpte-product-hover-delay-lg {
      transition-delay: 0.7s !important;
    }

    .wpte-product-hover-delay-xl {
      transition-delay: 0.875s !important;
    }
    .wpte-product-hover-delay-xxl {
      transition-delay: 1.05s !important;
    }
    .wpte-product-hover-delay-xxl,
    .wpte-product-hover-delay-xl,
    .wpte-product-hover-delay-lg,
    .wpte-product-hover-delay-md,
    .wpte-product-hover-delay-sm,
    .wpte-product-hover-delay-xs {
      opacity: 1;
    }
  }
  .wpte-product-hover-figure-heading,
  .wpte-product-hover-heading,
  .wpte-product-hover-content,
  .wpte-product-btn {
    transition: all 0.25s ease 0.25s;
  }

  .wpte-data-animation {
    opacity: 0;
    visibility: hidden;
  }
  .animated.wpte-data-animation {
    opacity: 1;
    visibility: visible;
  }

  .wpte-product-hover-style {
    .product-hover-align-top-left {
      justify-content: flex-start !important;
      text-align: left;
    }
    .product-hover-align-top-center {
      justify-content: flex-start !important;
      text-align: center;
    }
    .product-hover-align-top-right {
      justify-content: flex-start !important;
      text-align: right;
    }
    .product-hover-align-center-left {
      justify-content: center !important;
      text-align: left;
    }
    .product-hover-align-center-center {
      justify-content: center !important;
      text-align: center;
    }
    .product-hover-align-center-right {
      justify-content: center !important;
      text-align: right;
    }
    .product-hover-align-bottom-left {
      justify-content: flex-end !important;
      text-align: left;
    }
    .product-hover-align-bottom-center {
      justify-content: flex-end !important;
      text-align: center;
    }
    .product-hover-align-bottom-right {
      justify-content: flex-end !important;
      text-align: right;
    }
  }
}
