// Filter
.wpte-product-filter-wrapper {
  form {
    display: block;
  }
  .wpte-product-filter-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .wpte-product-filter-left {
      display: flex;
      align-items: center;
      justify-content: start;
    }
    .wpte-product-filter-right {
      display: flex;
      align-items: center;
      justify-content: end;
    }
    .wpte-product-filter-item {
      position: relative;
      box-sizing: border-box;
      vertical-align: middle;
      margin: 10px 6px 10px 0px;
      cursor: pointer;
      .wpte-product-filter-heading-normal {
        font-size: 16px;
        font-weight: 700;
        padding: 10px;
      }
      .wpte-product-filter-heading {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-width: 140px;
        font-size: 16px;
        font-family: 'Segoe UI', sans-serif;
        background: #fff;
        border: 1px solid rgba(0, 0, 0, 0.3);
        border-radius: 4px;
        box-shadow: 0 -2px 14px rgba(0, 0, 0, 0.025);
        transition: 0.05s background-color, 0.2s border-color, 0.2s box-shadow;
        padding: 10px 12px 10px 16px;
      }
      .wpte-product-filter-dropdown {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 4;
        display: none;
        visibility: hidden;
        min-width: 260px;
        max-height: 270px;
        overflow-y: auto;
        margin: 5px 0 0;
        padding: 8px 5px;
        text-align: left;
        list-style: none;
        background-color: #fff;
        -webkit-background-clip: padding-box;
        background-clip: padding-box;
        border: 1px solid rgba(0, 0, 0, 0.3);
        border-radius: 4px;
        cursor: default;
        -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
      }

      &:hover .wpte-product-filter-dropdown {
        display: block;
        visibility: visible;
      }
      &:after {
        content: '';
        display: none;
        position: absolute;
        width: 100%;
        height: 7px;
        bottom: -7px;
        left: 0;
        z-index: 1;
      }
      &:hover:after {
        display: block;
      }

      .wpte-filter-option {
        display: block;
        font-weight: 400;
        cursor: pointer;
        padding: 6px 12px 6px 14px;
      }
      .wpte-filter-option:last-child {
        margin-bottom: 0;
      }
      .wpte-product-filter-subcategory {
        padding-left: 20px;
      }
      .wpte-filter-option {
        .check-label {
          position: relative;
          font-size: 15px;
          font-family: 'Segoe UI', sans-serif;
          font-weight: normal;
          padding-left: 20px;
        }
        .cat-post {
          font-size: 11px;
          font-family: 'Segoe UI', sans-serif;
          font-weight: normal;
          background: #e6e6e6;
          line-height: 0;
          border-radius: 4px;
          padding: 1px 8px;
          margin-left: 2px;
        }
        // Start Checkbox Design
        [type='checkbox'] {
          display: none;
        }
        & .check-label:before {
          content: '';
          top: 2.5px;
          left: 0;
          width: 14px;
          height: 14px;
          position: absolute;
          border: 1px solid #aa4098;
          background-color: #ffffff;
        }
        & input[type='checkbox']:not(:checked) + .check-label:after {
          opacity: 0;
          transform: scale(0);
          transition: all 0.2s ease;
        }
        & input[type='checkbox']:checked + .check-label:after {
          left: 3px;
          top: 5.5px;
          content: '\e829';
          font-size: 9px;
          line-height: 1;
          color: #aa4098;
          position: absolute;
          opacity: 1;
          font-family: 'wpte-cart-icon';
          transform: scale(1);
          transition: all 0.2s ease;
        }
        // End checkbox design

        // Start Radio Button Design
        .radio-label {
          position: relative;
          font-size: 15px;
          font-family: 'Segoe UI', sans-serif;
          font-weight: normal;
          padding-left: 25px;
        }
        input[type='radio'] {
          display: none;
        }
        & .radio-label::before {
          content: '';
          top: 2.5px;
          left: 0;
          width: 15px;
          height: 15px;
          position: absolute;
          border: 1px solid #aa4098;
          border-radius: 50%;
          background-color: #ffffff;
        }

        & input[type='radio']:not(:checked) + .radio-label:after {
          opacity: 0;
          transform: scale(0);
        }

        & input[type='radio']:checked + .radio-label:after {
          left: 3.7px;
          top: 6px;
          content: '';
          border-radius: 50%;
          width: 10px;
          height: 10px;
          background: #aa4098;
          position: absolute;
          transition: all 0.2s ease;
          opacity: 1;
          transform: scale(1);
        }
        // End Radio Button Design

        // Start Rating Design
        .check-label {
          .wpte-icons {
            color: rgb(243 182 103);
            font-size: 18px;
          }
        }
        // End Rating Design
      }
	  .wpte-wc-color-attribute-filter{
		display: flex;
		flex-wrap: wrap;
	  }
    }
    .wpte-product-filter-item:last-child {
      margin-right: 0;
    }
    .wpte-sort-by-show,
    .wpte-per-page-show,
    .wpte-category-show,
    .wpte-rating-show,
    .wpte-price-range-show {
      display: inline-block;
    }
  }
}

.wpte-pl-cat-page-filter{
	.wpte-product-filter-items{
		justify-content: end;
		margin-bottom: 20px;
	}
}
