.wpte-quick-view-wrapper{
    width: 100%;
    position: relative;
}
.wpte-quick-view-wrapper a{
    text-decoration: none;
}

.wpte-quick-view-wrapper a:focus{
    border: none;
    box-shadow: none;
    outline: none;
}

#wpte-quick-view-box{
    font-size: 12px;
    display: grid;
    grid-template-columns: 51% 45.5%;
    gap: 30px;
}


/* Imgae Area */

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images{
    width: 100%;
}

.wpte-quick-view-image-area img{
    width: 100%;
    height: auto;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-thumbs li{
    float: none;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-thumbs{
    display: flex;
    flex-wrap: wrap;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-thumbs li:nth-child(4n){
    margin-right: 15px;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-thumbs li:nth-child(5n){
    margin-right: 0px;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-nav li{
    width: 72px;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-nav img{
    width: 72px;
    cursor: pointer;
    border-radius: 5px;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(95, 61, 249, 0);
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-nav img:hover {
    border-color: rgb(22, 62, 149);
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images .flex-control-nav  .flex-active {
    width: 72px;
    border-color: rgb(22, 62, 149);
    cursor: default;
}

/* Content Area */
.wpte-quick-view-content-area .wpte-quick-view-stock{
    background: #12A05C;
    color: #fff;
    display: inline-block;
    vertical-align: top;
    padding: 0 10px;
    border-radius: 20px;
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 20px;
}
.wpte-quick-view-content-area .wpte-quick-view-stock.out-stock{
    background: #752A2B;
    color: #fff;
}
.wpte-quick-view-content-area .wpte-quick-view-category a{
    display: inline-block;
    line-height: 20px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    color: rgba(91,108,143,1);
}
.wpte-quick-view-content-area .wpte-quick-view-title{
    margin-top: 6px;
}
.wpte-quick-view-content-area .wpte-quick-view-title a h1{
    font-size: 30px;
    color: rgba(16,49,120,1);
    font-style: normal;
    font-weight: 600;
    line-height: 1.3;
}

.wpte-quick-view-content-area .wpte-quick-view-description p{
    font-size: 14px;
    margin-bottom: 10px;
}
.wpte-quick-view-content-area .wpte-quick-view-rating{
    font-size: 14px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgb(239, 239, 239);
}
.wpte-quick-view-content-area .wpte-quick-view-rating a{
    color: rgba(16,49,120,1);
    display: inline-block;
    vertical-align: top;
    font-size: 90%;
    line-height: 18px;
    margin-left: 10px;
}
.wpte-quick-view-content-area .wpte-quick-view-rating a:hover{
    /* color: rgba(255,153,35,1); */
    text-decoration: underline;
}

.wpte-quick-view-content-area .wpte-quick-view-price .woocommerce-Price-amount bdi{
    /* color: rgba(255,153,35,1); */
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
}
.wpte-quick-view-content-area .wpte-quick-view-price .woocommerce-Price-amount{
    margin-right: 5px;
    margin-left: 5px;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-price p.price{
    /* color: rgba(255,153,35,1); */
    display: flex;
    align-items: center;
}
.wpte-quick-view-content-area .wpte-quick-view-price{
    margin: 30px 0 30px 0;
}

/* Cart Button Area */
/* .wpte-hide-qty .quantity{
    display: none;
} */
.wpte-hide-qty .woocommerce-grouped-product-list-item__quantity .quantity{
    display: block;
}
.wpte_quick_view_product_addtocart_button_container .wpte-quick-view-quantity .wpte-quick-view-qty-title{
    display: block;
    line-height: 20px;
    font-size: 16px;
    color: rgba(16,49,120,1);
    font-style: normal;
    font-weight: 500;
    padding-bottom: 8px; 
}
.wpte_quick_view_product_addtocart_button_container .stock{
    padding-bottom: 10px; 
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity{
    margin-right: 15px;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity {
    background: rgba(240,242,245,1);
    width: 130px;
    height: 46px;
    position: relative;
    letter-spacing: 0;
    border-radius: 60px;
    box-sizing: border-box;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity .quantity{
    background: rgba(240, 242, 245, 0);
    width: 60px;
    height: 40px;
    position: unset;
    letter-spacing: 0;
    border-radius: unset;
    box-sizing: border-box;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .wpte-quick-view-quantity-minus,
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .wpte-quick-view-quantity-plus{
    color: rgba(91,108,143,1);
    width: 30px;
    height: 44px;
    position: absolute;
    top: 0;
    right: 10px;
    margin: 0;
    border-width: 0;
    cursor: pointer;
    font-size: 20px;
    background: transparent;
    line-height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity div.quantity .wpte-quick-view-quantity-minus,
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity div.quantity .wpte-quick-view-quantity-plus{
    display: none;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .wpte-quick-view-quantity-minus{
    left: 10px;
    right: auto;
}

.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .qty{
    color: rgba(91,108,143,1);
    width: 100%;
    max-width: 100%;
    margin: 0;
    padding: 8px 30px;
    border-width: 0;
    line-height: 30px;
    text-align: center;
    background: rgba(240,242,245,1);
    font-size: 18px;
    border-radius: 60px;
    
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity div.quantity .qty {
    color: rgba(91,108,143,1);
    width: 60px;
    height: 40px;
    margin: unset;
    padding: unset;
    border-width: 1px;
    line-height: 30px;
    text-align: center;
    border-radius: 5px;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .qty::-webkit-outer-spin-button,
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .qty::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity div.quantity .qty::-webkit-outer-spin-button,
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart .woocommerce-grouped-product-list-item__quantity div.quantity .qty::-webkit-inner-spin-button {
  -webkit-appearance: auto;
  -moz-appearance: auto;
  appearance: auto;
  margin: 0;
}
.wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity .qty:focus{
    box-shadow: none;
    border: none;
    outline: none;
}

.wpte-quick-view-wrapper .woocommerce .wpte_quick_view_product_addtocart_button_container button.button {
    /* border-color: rgba(255,153,35,1);
    background: rgba(255,153,35,1); */
    color: rgba(255,255,255,1);
    overflow: hidden;
    min-width: 190px;
    vertical-align: middle;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    padding: 10px 25px;
    box-shadow: none;
    text-shadow: none;
    border-width: 0;
    border-radius: 60px;
    filter: none;
    cursor: pointer;
    background-image: none;
    text-align: center;
    border-width: 1px;
    border-style: solid;
}

/* Tags and Skus */
.wpte-quick-view-meta-content{
    margin-top: 30px;
    padding-top: 20px;

}
.wpte-quick-view-meta-content .product_meta .sku_wrapper,
.wpte-quick-view-meta-content .product_meta .tagged_as,
.wpte-quick-view-meta-content .product_meta .posted_in{
    color: rgba(16,49,120,1);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    margin-right: 10px;
    margin-bottom: 5px;
    line-height: 1.7;

}
.wpte-quick-view-meta-content .product_meta .posted_in{
    display: none !important;
}
.wpte-quick-view-meta-content .product_meta .sku_wrapper .sku,
.wpte-quick-view-meta-content .product_meta .tagged_as a,
.wpte-quick-view-meta-content .product_meta .posted_in a{
    color: rgba(91,108,143,1);
    font-size: 13px;
    font-style: normal;
    font-weight: normal;
}

/* Quick View */
#wpte-popup-display-notice ul{
    width: 450px;
    position: fixed;
    z-index: 9999;
    right: 20px;
    top: 40px;
    background: #fbd7d7;
}
.wpte-quick-view-alert-close{
    float: right;
    margin-right: -18px;
    margin-top: -18px;
    font-size: 24px;
    cursor: pointer;
}


@media (max-width: 575.98px) { 
    #wpte-quick-view-box{
        grid-template-columns: repeat(1, 1fr);
        width: 85%;
        max-height: 500px;
    }
    .wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity{
        width: 202px;
        margin-bottom: 15px;
    }

    .wpte-product-popup-close {
        right: 10px;
        top: 0px;
        font-size: 24px;
    }

    #wpte-popup-display-notice ul {
        width: 95%;
        right: 10px;
        top: 30px;
    }

    .wpte-quick-view-alert-close {
        margin-right: -90px;
        margin-top: -8px;
        cursor: pointer;
        width: 100%;
        text-align: right;
    }
}

/* Medium devices (landscape Tab, 576px and up) */
@media (min-width: 576px) and (max-width: 768px) { 
    #wpte-quick-view-box{
        width: 80%;
        max-height: 400px;
    }
    .wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity{
        width: 204px;
        margin-bottom: 15px;
    }
}

/* Medium devices (landscape Tab, 576px and up) */
@media (min-width: 768.02px) and (max-width: 1224px) { 
    #wpte-quick-view-box{
        grid-template-columns: 44% 50.5%;
        width: 80%;
        max-height: 500px;
    }
    .wpte-quick-view-wrapper .woocommerce div.product .wpte-quick-view-image-area div.images {
        width: 100%;
    }
    .wpte-quick-view-wrapper .woocommerce div.product .wpte_quick_view_product_addtocart_button_container form.cart div.quantity{
        width: 110px;
        margin-bottom: 0;
    }

    .wpte-quick-view-wrapper .woocommerce .wpte_quick_view_product_addtocart_button_container button.button {
        max-width: 190px;
        font-size: 16px;
        min-width: 100px;
    }

    .wpte-quick-view-wrapper .woocommerce div.product form.cart .button.single_add_to_cart_button {
        padding: 10px 22px;
    }
}